     prop=\"car_num\"></el-table-column>\n        <el-table-column align=\"center\"\n                         label=\"救援编号\"\n                         prop=\"res_num\"></el-table-column>\n        <el-table-column align=\"center\"\n                         label=\"车辆图片\">\n          <template slot-scope=\"scope\">\n            <img :src=\"scope.row.car_img\"\n                 class=\"car_img\">\n          </template>\n        </el-table-column>\n        <el-table-column align=\"center\"\n                         label=\"创建时间\"\n                         prop=\"create_time\"></el-table-column>\n        <el-table-column align=\"center\"\n                         label=\"操作\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click=\"details(scope.row),detailsVisible=true\">详情</el-button>\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click=\"audit(scope.row,scope.$index)\"\n                       :loading=\"Loading[scope.$index]\">审核</el-button>\n          </template>\n        </el-table-column>\n      </el-table>\n      <!-- 详情 -->\n      <el-dialog title=\"详情\"\n                 :visible.sync=\"detailsVisible\"\n                 center>\n        <div v-if='detailsList'\n             class=\"details\">\n          <div>\n            <span>维修厂名称 : </span>\n            <span>{{detailsList.company}}</span>\n          </div>\n          <div>\n            <span>创建时间 : </span>\n            <span>{{detailsList.create_time}}</span>\n          </div>\n          <div>安全员信息：</div>\n        </div>\n        <div>\n          <table>\n            <tr>\n              <th>姓名</th>\n              <th>照片</th>\n              <th>身份证</th>\n              <th>驾驶证</th>\n            </tr>\n            <tr v-for='(item,index) in detailsList.safe_crew'\n                :key=\"index\">\n              <td>\n                {{item.name}}\n              </td>\n              <td>\n                <img :src=\"item.headPic\"\n                     class=\"img\"\n                     ref=\"img\">\n              </td>\n              <td>\n                <img :src=\"item.z_idCard\"\n                     class=\"img\"\n                     ref=\"img\">\n              </td>\n              <td>\n                <img :src=\"item.driving\"\n                     class=\"img\"\n                     ref=\"img\">\n              </td>\n            </tr>\n          </table>\n        </div>\n\n      </el-dialog>\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n      </div>\n    </div>\n\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\n//  import page from '../common.js';\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      authList: [],\n      threeAuthList: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 0,\n      seCurId:0,\n      pageCount: 0,\n      list: [],\n      detailsVisible: false,\n      detailsList: {},\n      Loading: []\n    }\n  },\n  methods: {\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    async init () {\n      try {\n        const res = await this.$axios.post('admin/ShopAudit/serveHandle', { token: this.token, page: this.page })\n        this.list = res.data.data.list || []\n        this.pageCount = res.data.data.rows;\n      } catch (err) {\n        throw (err)\n      }\n    },\n    async details (row) {  //详情\n      try {\n        const res = await this.$axios.post('admin/ShopAudit/carDetail', { token: this.token, id: row.id })\n        this.detailsList = res.data.data || {}\n        this.$nextTick(() => {\n          const img = this.$refs.img;\n          Viewer(img)\n        })\n      } catch (err) {\n        throw (err)\n      }\n    },\n    async audit (row, index) {  //通过审核\n      try {\n        console.log(index)\n        this.Loading[index] = true\n        const res = await this.$axios.post('admin/ShopAudit/carHandle', { token: this.token, id: row.id })\n        this.Loading[index] = false\n        if (res.data.code == 1) {\n          this.init()\n          this.$message({ message: res.data.msg, type: 'success' })\n        } else {\n          this.$message.error(res.data.msg)\n        }\n      } catch (err) {\n        this.Loading[index] = false\n        throw (err)\n      }\n    },\n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: this.token,\n        id: id\n      })\n        .then(res => {\n\n          if (res.data.code == 1) {\n            var arr = res.data.data;\n            for (var i = 0; i < arr.length; i++) {\n\n              if (arr[i].name == '车补审核') {\n                this.seCurId = arr[i].id;\n\n                if (arr[i].son) {\n\n                  this.threeAuthList = arr[i].son;\n                  for (var j = 0; j < arr[i].son.length; j++) {\n                    if (arr[i].action != arr[i].son[j].action) {\n                      arr[i].action = arr[i].son[0].action;\n                    }\n                    if (arr[i].son[j].name == '待审核' && arr[i].name == '车补审核') {\n                      this.thCurId = arr[i].son[j].id;\n                    }\n\n                  }\n                }\n              }\n            }\n            this.authList = arr;\n          } else {\n            this.$alert(res.data.msg, '提示', { type: 'error' });\n          }\n        })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n\n  mounted () {\n    this.init()\n    this.erAuth()\n  },\n\n}\n</script>\n<style scoped>\n.car_img {\n  width: 50px;\n  height: 50px;\n  vertical-align: middle;\n  margin: 10px 0;\n}\n.details > div {\n  margin: 10px 0;\n}\n.details span:first-child {\n  width: 100px;\n  text-align: right;\n  display: inline-block;\n}\n.img {\n  width: 50px;\n  height: 50px;\n  vertical-align: middle;\n  margin: 10px 0;\n}\n</style>\n"],"sourceRoot":""}]);

